@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 2em;
    height: 3.5em;
    font-weight: 500;
    &:focus {
        box-shadow: none;
    }
    svg,
    img{
        width: 1.25em;
        margin: 0 .625em;
        &:first-child{
            margin-left: 0;
        }
        &:last-child{
            margin-right: 0;
        }
    }
}

.m-btn-small{
    height: 2.875em;
    padding: 0 1.5em;
    span{
        font-size: .875em;
    }
    &-arrow{
        padding-right: 1em;
    }
}

.m-btn-medium{
    padding: 0 1.5em;
    height: 3.25em;
    span{
        font-size: .875em;
    }
}

.m-btn-arrow{
    padding: 0 .875em 0 1.5em;
    height: 3.25em;
    img{
        margin-left: .5em;
    }
    span{
        font-size: .875em;
    }
}

.m-btn-blue{
    background-color: $primary;
    color: $white;
    &:hover{
        background-color: darken($primary, 10%);
        color: $white;
    }
}

.m-btn-blue-outline{
    border: 1px solid $primary;
    color: $primary;
    &:hover{
        background-color: rgba($primary, .25);
        border-color: rgba($primary, .25);
        color: $primary;
        ._stroke{

        }
    }
}

.m-btn-gray{
    background-color: $bg;
    color: $black;
    &:hover{
        background-color: $primary;
        color: $white;
    }
}

.m-btn-gray-outline{
    border: $border;
    color: $primary;
    &:hover{
        background-color: rgba($primary,10%);
        color: $primary;
    }
}

.m-btn-white{
    background-color: $white;
    color: $black;
    &:hover{
        background-color: lighten($primary, 30%);
        color: $black;
    }
}

.m-btn-white-outline{
    border: 1px solid $white;
    color: $white;
    &:hover{
        background-color: $white;
        border-color: $white;
        color: $primary;
    }
}

.text-btn{
    font-weight: 500;
    color: $primary;
    display: flex;
    align-items: center;
    &:hover{
        color: $primary;
        opacity: .8;
    }
}

.round-btn{
    @include center;
    width: 3.125em;
    height: 3.125em;
    border-radius: 50%;
    img{
        width: 1.125em;
    }
}


@media screen and (max-width: 992px){
    .m-btn{
        height: 3.375em;
        span{
            font-size: .875em;
        }
    }

    .m-btn-medium-tablet{
        height: 3em;
        span{
            font-size: .75em;
        }
    }

    .m-btn-arrow{
        span{
            font-size: .75em;
        }
    }

    ._full-mob{
        width: 100%;
    }
}
