.advantage-card2{
  padding: 2em;
  border: $border;
  &__icon{
    height: 3em;
    margin-bottom: 1.5em;
    img{
      height: 100%;
      width: auto;
      display: block;
    }
  }
}

.about-nums{
  &__top{
    max-width: 48.75em;
  }
  &__item{
    padding: 2em;
    border: $border;
  }
}

.about-manufacture{
  &__top{
    max-width: 60.375em;
  }
}

.manufacture-card{
  &__img{
    @include imgCover(112.5%);
  }
}

.about-main{
  background-size: cover;
  color: $white;
  &:after{
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.60) 0%, rgba(0, 0, 0, 0.60) 100%);
  }
  .breadcrumbs{
    color: rgba($white,.5);
  }
  &__inner{
    display: flex;
    flex-direction: column;
    min-height: 37.5em;
    position: relative;
    z-index: 2;
    padding-bottom: 5em;
  }
  &__content{
    max-width: 46.625em;
    margin-top: auto;
  }
}

.contacts-main{
  &__info{
    max-width: 44em;
  }
  &__items{
    display: flex;
    flex-wrap: wrap;
    gap: 2.5em 4em;
  }
}

.error404{
  background-size: cover;
  background-position: center;
  flex-grow: 1;
  flex-direction: column;
  @include center;
  color: $white;
  text-align: center;
  padding: 5em 0;
  &:after{
    background: linear-gradient(270deg, #000 0%, rgba(0, 0, 0, 0.00) 100%);
  }
  &__inner{
    position: relative;
    z-index: 2;
  }
  &__num{
    font-size: 18.75em;
    font-weight: 700;
  }
}


@media screen and (max-width: 992px){
  .about-nums{
    &__item{
      padding: 1.375em;
    }
    @media screen and (min-width: 577px){
      &__grid{
        grid-template-columns: repeat(6,calc(100% / 6 - 7.5em / 6));
      }
      &__item{
        grid-column: span 2;
        &:nth-child(1),
        &:nth-child(2){
          grid-column: span 3;
        }
      }
    }
  }

  .about-manufacture{
    &__grid{
      row-gap: 2.5em;
    }
    .manufacture-card:last-child{
      grid-column: 1/-1;
    }
  }

  .manufacture-card{
    &__img{
      height: 25em;
      padding: 0;
    }
  }

  .about-main{
    &__inner{
      min-height: 27.5em;
      padding-bottom: 4em;
    }
  }

  .error404{
    padding: 4em 0;
    &__num{
      font-size: 12.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .advantage-card2{
    padding: 1.25em;
    display: flex;
    &__icon{
      width: 3em;
      height: auto;
      margin: 0 1em 0 0;
      img{
        height: auto;
        width: 100%;
      }
    }
  }

  .about-nums{
    &__item{
      padding: 1.25em;
    }
  }

  .about-manufacture {
    &__grid {
      row-gap: 1.5em;
    }
  }

  .manufacture-card{
    &__img{
      height: 20.5em;
    }
  }

  .about-main{
    &__inner{
      min-height: 38.75em;
      padding-bottom: 3em;
    }
  }

  .contacts-main{
    &__items{
      gap: 1.5em;
    }
  }

  .error404{
    padding: 4em 1em;
    &__inner{
      width: 100%;
    }
    &__num{
      font-size: 8em;
    }
  }
}
