._max-w856{
  max-width: 53.5em;
}

.manufacturer{
  .text{
    gap: 1em;
  }
}

.check-card{
  &__icon{
    height: 2em;
    display: block;
  }
}


.sale-slider{
  .swiper-slide{
    width: 50%;
  }
}

.sale-card{
  height: 100%;
  min-height: 22.375em;
  padding: 2.5em;
  background-size: cover;
  &__info{
    max-width: 26em;
  }
}

.materials-slider{
  @media screen and (min-width: 577px){
    .swiper-wrapper{
      transform: unset !important;
      display: grid;
      grid-template-columns: repeat(3,calc(100% / 3 - 1em));
      gap: 2em 1.5em;
    }
    .swiper-container{
      width: 100%;
    }
    .swiper-slide{
      padding: 0;
    }
  }
}

.material-card{
  &__img{
    @include imgCover(0);
    height: 14.625em;
  }
}

.video-card{
  .blog-card__img{
    &:before{
      content: '';
      @include centerAbsolute;
      width: 3.125em;
      height: 3.125em;
      background-image: url(../img/play.svg);
      @include bgImgSize(100%);
      z-index: 2;
    }
  }
}

.we-cheaper{
  &__top{
    position: relative;
  }
  &__badge{
    @include center;
    padding: 0 1.5em;
    border: $border;
    height: 3em;
    border-radius: 12.5em;
    @media screen and (min-width: 993px){
      position: absolute;
      right: 0;
      bottom: -.125em;
    }
  }
}

.send-req{
  &__inner{
    padding: 4em;
    background-color: $primary;
    color: $white;
  }
  &__col{
    display: flex;
    flex-direction: column;
  }
  &__bottom{
    margin-top: auto;
    padding-top: 1.5em;
  }
}


@media screen and (max-width: 992px){
  .we-check{
    &__grid{
      grid-template-columns: repeat(6,calc(100% / 6 - 7.5em / 6));
      row-gap: 2.5em;
    }
    .check-card{
      grid-column: span 2;
      &:nth-child(4),
      &:nth-child(5){
        grid-column: span 3;
      }
    }
  }

  .sale-card{
    padding: 2em;
  }

  .materials-slider{
    @media screen and (min-width: 577px){
      .swiper-wrapper{
        grid-template-columns: repeat(2,calc(50% - .75em));
        gap: 2.5em 1.5em;
      }
    }
  }

  .we-cheaper{
    &__top{
      display: flex;
      flex-direction: column;
      align-items: flex-start;
    }
    &__badge{
      order: -1;
      margin: 0 0 1.5em;
      height: 2.75em;
    }
  }

  .send-req{
    &__inner{
      padding: 2.5em;
    }
    .h2{
      br{
        display: none;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .we-check{
    &__grid{
      grid-template-columns: 100%;
      gap: 1.5em;
    }
    .check-card{
      grid-column: span 1 !important;
    }
  }

  .check-card{
    display: flex;
    align-items: flex-start;
    &__icon{
      height: auto;
      width: 2em;
      flex-shrink: 0;
    }
    &__content{
      margin: 0 0 0 1em;
    }
  }

  .sale{
    &__top{
      display: block;
    }
    &__link{
      margin-top: 1.5em;
      width: 100%;
    }
  }

  .sale-slider{
    .swiper-slide{
      width: 100%;
    }
  }

  .materials-slider{
    .swiper-container{
      width: calc(100% + .75em);
    }
    .swiper-slide{
      width: 18.25em;
      padding-right: .75em;
    }
  }

  .send-req{
    &__inner{
      padding: 1.5em;
    }
  }
}
