.tabs{
  border-bottom: $border;
  &__items{
    display: flex;
    margin-bottom: -1px;
    width: calc(100% + 2em);
    margin-left: -1em;
    overflow-x: auto;
    padding: 0 1em;
    &::-webkit-scrollbar {
      width: 0;
      height: 0;
    }
  }
}

.tab{
  padding: 0 0 .875em;
  display: flex;
  justify-content: center;
  font-weight: 700;
  flex-shrink: 0;
  transition: all .3s;
  border-bottom: 2px solid transparent;
  @include lastM(0 2.5em 0 0);
  &:hover{
    color: $primary;
    cursor: pointer;
  }
  &._active{
    border-color: $primary;
    pointer-events: none;
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}
