.footer{
  overflow: hidden;
  margin-top: 2.5em;
  &._mt0{
    margin-top: 0;
    .footer-top{
      border-top: none;
    }
  }
  &__logo{
    display: block;
    width: 10.625em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.footer-top{
  padding: 3em 0;
  border-top: $border;
  border-bottom: $border;
  &__inner{
    display: grid;
    grid-template-columns: 15.5em 30.5em 23em;
    justify-content: space-between;
    position: relative;
  }
  &__col2{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.25em));
    gap: 2.5em;
  }
  &__col3{
    color: $white;
    &:before{
      content: '';
      position: absolute;
      top: -3em;
      transform: translateY(-1px);
      left: 55em;
      width: 100vw;
      height: calc(100% + 6em + 2px);
      background-color: $primary;
    }
  }
}

.footer-nav{
  &__links{
    margin-top: .75em;
    @include grid100gap(.625em);
    color: $gray;
  }
}

.footer-contacts{
  position: relative;
  z-index: 2;
  height: 100%;
  display: flex;
  flex-direction: column;
  &__nav{
    display: flex;
    margin-top: auto;
    padding-top: 2em;
  }
  a{
    &:hover{
      color: rgba($white,.7);
    }
  }
}

.footer-bottom{
  &__inner{
    gap: 1em;
    padding: 1em 0;
  }
  &__desc{
    margin-left: auto;
  }
}

.mob-footer{
  display: none;
}


@media screen and (max-width: 992px){
  .footer{
    margin-top: 2em;
    &__logo{
      width: 10.57em;
    }
  }

  .footer-top{
    padding: 2em 0;
    &__inner{
      grid-template-columns: 21em 19.5em;
    }
    &__col{
      position: absolute;
      top: 0;
      left: 0;
    }
    &__col2{
      grid-template-columns: repeat(2,calc(50% - .75em));
      gap: 1.5em;
      position: relative;
      padding-top: 4.5em;
    }
    &__col3{
      &:before{
        top: -2em;
        left: 22.875em;
        height: calc(100% + 4em + 2px);
      }
    }
  }

  .footer-bottom{
    &__inner{
      flex-wrap: wrap;
    }
    &__desc{
      width: 100%;
      margin: 0;
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    padding-bottom: 3.75em;
  }

  .footer-top{
    padding-bottom: 0;
    border-bottom: none;
    margin-top: 1.5em;
    &__inner{
      grid-template-columns: 100%;
    }
    &__col2{
      grid-template-columns: repeat(2,calc(50% - .375em));
      gap: .75em;
      padding-bottom: 2em;
    }
    &__col3{
      padding: 2em 0;
      position: relative;
      &:before{
        top: 0;
        transform: unset;
        left: -1em;
        height: 100%;
        width: calc(100% + 2em);
      }
    }
  }

  .footer-contacts{
    &__link{
      width: 100%;
    }
  }

  .footer-bottom{
    &__inner{
      gap: .75em;
      flex-direction: column;
      align-items: flex-start;
      flex-wrap: unset;
    }
  }

  .mob-footer{
    position: fixed;
    left: 0;
    bottom: 0;
    z-index: 10;
    padding: .625em 0;
    background-color: $white;
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    border-top: $border;
    &__btn{
      @include center;
      flex-direction: column;
      color: $gray;
      position: relative;
      img{
        height: 1.25em;
        margin-bottom: .125em;
      }
      span{
        font-size: .8125em;
        line-height: 131%;
      }
      i{
        position: absolute;
        left: 50%;
        top: -.1875em;
        background-color: $primary;
        border-radius: 50%;
        width: .75em;
        height: .75em;
      }
    }
  }
}
