.fg,
.m-select{
  position: relative;
  label{
    display: block;
    font-size: 1em;
    line-height: 150%;
    font-weight: 600;
    margin-bottom: .5em;
    span{
      color: $primary;
    }
  }
}

.fg {
  margin: 0;
  .nice-select,
  input,
  textarea{
    border-radius: 0;
    border: $border;
    padding: 0 1em;
    height: 3.5em;
    width: 100%;
    font-size: 1em;
    background-color: transparent;
    color: $black;
    &::placeholder {
      color: $gray;
    }
  }
  .nice-select{
    display: flex;
    align-items: center;
    justify-content: space-between;
    white-space: nowrap;
    padding-right: 3em;
    float: unset;
    &:before{
      content: '';
      order: 3;
      flex-shrink: 0;
      width: 1.5em;
      height: 1.5em;
      background-image: url(../img/select-arrow.svg),url(../img/select-arrow2.svg);
      @include bgImgSize(100%);
      background-size: 100% auto, 0 auto;
      right: .875em;
      top: 50%;
      transform: translateY(-50%);
      position: absolute;
    }
    &.open{
      &:before{
        background-size: 0 auto,100% auto;
      }
    }
    .current{
      font-size: 1em;
      line-height: 150%;
      width: 100%;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
    }
    .list{
      margin-top: .375em;
      border-radius: .375em;
      box-shadow: 0px 6px 16px 0px rgba(20, 20, 20, 0.10);
      border: none;
      background: #FFF;
      max-height: 20.75em;
      overflow-y: auto;
      z-index: 5;
      @include scroll;
      padding: .375em;
    }
    .option{
      font-size: 1em;
      line-height: 150%;
      padding: .5em;
      display: flex;
      align-items: center;
      color: $black;
      border: none;
      border-radius: .25em;
      &:last-child{
        border-bottom: none;
      }
      &.disabled{
        display: none;
      }
      &:hover,
      &.focus,
      &.selected.focus{
        background-color: $bg;
        font-weight: 400;
        color: $black;
      }
    }
  }
  textarea{
    padding-top: 1.19em;
    padding-bottom: 1.19em;
    height: 7.28em;
    resize: none;
  }
}

.fg-white{
  textarea,
  input{
    background-color: $white;
    border-color: $white;
  }
}

.select-option,
.sort-item,
.m-switch,
.btn-radio,
.color-radio,
.m-radio,
.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check,
.m-radio{
  label{
    position: relative;
    cursor: pointer;
    display: block;
    padding-left: 1.875em;
    min-height: 1.25em;
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 0;
      width: 1.25em;
      height: 1.25em;
      @include bgImgSize(0);
      transition: all .0s;
      border: $border;
    }
    a{
      text-decoration: underline;
      position: relative;
      z-index: 2;
    }
  }
  .text14{
    line-height: 143%;
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        background-size: 100% auto;
        border-color: $primary;
        background-color: $primary;
      }
    }
  }
  label {
    &:before {
      background-image: url(../img/check.svg);
      background-size: 0 auto;
    }
  }
  &._white{
    input {
      &:checked+label {
        &::before {
          border-color: $white;
          background-color: $white;
        }
      }
    }
    label {
      &::before {
        border-color: $white;
      }
    }
  }
}

.m-radio {
  input {
    &:checked+label {
      &::before {
        background-size: 0 auto , 100% auto;
      }
    }
  }
  label {
    padding-top: 0;
    padding-left: 2.125em;
    display: flex;
    align-items: center;
    &:before {
      background-image: url(../img/radio.svg),url(../img/radio2.svg);
      background-size: 100% auto, 0 auto;
      width: 1.5em;
      height: 1.5em;
      top: 50%;
      transform: translateY(-50%);
      border: none;
    }
  }
}

.cartcalc{
  max-width: 100%;
  width: 8em;
  height: 2.375em;
  position: relative;
  input{
    background-color: $bg;
    border: none;
    color: $black;
    font-weight: 500;
    font-size: .875em;
    text-align: center;
    width: 100%;
    height: 100%;
    border-radius: 0;
  }
  svg{
    width: .75em;
    height: auto;
  }
  button{
    @include center;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
  }
  .ccalc-minus{
    left: .625em;
  }
  .ccalc-plus{
    right: .625em;
  }
}


@media screen and (max-width: 992px){
  .m-check,
  .m-radio{
    label{
      padding-top: .1875em;
    }
  }

  .m-radio {
    label {
      padding-top: .0625em;
      &:before {
        top: 0;
      }
    }
  }

  .cartcalc{
    height: 2.375em;
    width: 6.5em;
    input{
      font-size: .75em;
    }
  }
}


