.products-slider{
  .swiper-slide{
    width: 25%;
  }
}

.tags{
  &__items{
    display: flex;
    flex-wrap: wrap;
    margin-top: -1em;
    .tag-item{
      margin: 1em 1em 0 0;
    }
  }
  &__more{
    display: none;
  }
}

.tag-item{
  border-radius: 12.5em;
  color: $black;
}

.interior{
  overflow: hidden;
}

.interior-slider{
  &:first-child{
    margin-top: 0;
  }
  &__img{
    @include imgCover(0);
    height: 14.625em;
  }
  @media screen and (min-width: 577px){
    .swiper-wrapper{
      transform: unset !important;
      display: grid;
      grid-template-columns: repeat(3,calc(100% / 3 - 1em));
      gap: 1.5em;
    }
    .swiper-container{
      width: 100%;
    }
    .swiper-slide{
      padding: 0;
    }
  }
}

.product-detail{
  .props,
  .text{
    max-width: 58.75em;
  }
}

.props{
  @include grid100gap(.75em);
}

.prop-item{
  display: flex;
  justify-content: space-between;
  gap: .5em;
  &:after{
    content: '';
    flex-grow: 1;
    margin-left: 1em;
    height: 1px;
    width: .25em;
    border-bottom: 1px dashed $line;
    margin-top: .875em;
  }
  &__title{
    color: $gray;
  }
  &__desc{
    order: 2;
  }
}


@media screen and (max-width: 992px){
  .interior-slider{
    @media screen and (min-width: 577px){
      .swiper-wrapper{
        grid-template-columns: repeat(2,calc(50% - .75em));
        gap: 1.5em;
      }
      .swiper-pagination{
        display: none;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .tags{
    &__items{
      width: calc(100% + .75em);
      margin-top: -.75em;
      .tag-item{
        margin: .75em .75em 0 0;
      }
    }
    &__more{
      display: flex;
      margin: .75em .75em 0 0;
      border-radius: 12.5em;
      ._view2{
        display: none;
      }
    }
    &._show-all{
      .tags__more{
        ._view1{
          display: none;
        }
        ._view2{
          display: block;
        }
      }
    }
    &:not(._show-all){
      .tag-item:nth-child(5)~.tag-item{
        display: none;
      }
    }
  }

  .tags__more,
  .tag-item{
    padding: 0 .75em;
    height: 2.625em;
  }

  .interior-slider{
    .swiper-slide{
      width: 100%;
    }
    &__img{
      height: 14em;
    }
  }
}
