@import "../vars.scss";
.breadcrumbs {
    margin: 1.5em 0;
    color: $gray;
    li{
        display: inline;
        position: relative;
        &:not(:first-child) {
            margin-left: 2.5em;
            &:before {
                content: '';
                position: absolute;
                top: 0;
                right: 100%;
                margin-right: 1em;
                font-size: .875em;
                line-height: 157%;
            }
        }
        &:last-child{
            pointer-events: none;
        }
    }
    a,span{
        font-size: .875em;
        line-height: 157%;
    }
}


@media screen and (max-width: 992px){
    .breadcrumbs{

    }
}


@media screen and (max-width: 576px){
    .breadcrumbs{
        height: 1.5em;
        overflow: hidden;
        margin: 0;
        ul{
            display: none;
        }
    }
}
