.info{
  &__inner{
    max-width: 67.25em;
  }
  &__more{
    display: none;
  }
}

.rating{
  display: flex;
}

.rating-input {
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
  position: relative;
  &--read-only {
    pointer-events: none;
  }
  &__field {
    position: absolute;
    left: 0;
    top: 100%;
    opacity: 0;
    pointer-events: none;
  }
  &__item {
    flex-shrink: 0;
    transition: all .3s;
    width: 1.25em;
    margin-right: .375em;
    svg {
      width: 100%;
      height: auto;
    }
    path {
      fill: $gray;
      stroke: $gray;
    }
    &:hover {
      cursor: pointer;
      path {
        fill: #F8B808;
      }
      &~.rating-input__item {
        path {
          fill: #F8B808;
        }
      }
    }
  }
}

.rating-input[data-total-rating="1"]:not(:hover) .rating-input__item:nth-child(n+5),
.rating-input[data-total-rating="2"]:not(:hover) .rating-input__item:nth-child(n+4),
.rating-input[data-total-rating="3"]:not(:hover) .rating-input__item:nth-child(n+3),
.rating-input[data-total-rating="4"]:not(:hover) .rating-input__item:nth-child(n+2),
.rating-input[data-total-rating="5"]:not(:hover) .rating-input__item:nth-child(n+1) {
  path {
    fill: #F8B808;
    stroke: #F8B808;
  }
}

.reviews-slider{
  .swiper-slide{
    width: calc(100% / 3);
  }
}

.review-card{
  border: $border;
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 1.9375em;
  &__name{
    img{
      width: 1.5em;
      margin-left: .5em;
    }
  }
  &__text{
    height: 12.125em;
    overflow: hidden;
  }
  .text{
    font-size: .875em;
    p{
      line-height: 157%;
    }
  }
  &__bottom{
    margin-top: auto;
    padding-top: 1em;
  }
  &__product{
    &-img{
      width: 4.375em;
      padding: .25em .375em;
      flex-shrink: 0;
      @include center;
      margin-right: 1em;
      img{
        width: 100%;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .review-card{
    padding: 1.4375em;
    &__text{
      height: 9em;
    }
    .text{
      font-size: .75em;
      p{
        line-height: 167%;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .info{
    &:not(._show-all){
      .text{
        *:nth-child(2)~*{
          display: none;
        }
      }
    }
    &._show-all{
      .info__more{
        ._view2{
          display: block;
        }
        ._view1{
          display: none;
        }
        img{
          transform: rotate(-180deg);
        }
      }
    }
    &__more{
      display: flex;
      align-items: center;
      color: $primary;
      margin-top: 1.5em;
      img{
        width: 1.25em;
        margin-left: .625em;
        transition: all .3s;
      }
      ._view2{
        display: none;
      }
    }
  }

  .faq{
    &__link{
      width: 100%;
    }
  }

  .review-card{
    padding: 1.1875em;
  }
}
