.free-ship{
  &__item{
    @include center;
    padding: 1.25em;
    background-color: $bg;
    height: 12em;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
}

.filter{
  &__grid{
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;
    gap: 2em 2.5em;
  }
  &__nav,
  &__top{
    display: none;
  }
}

.catalog-nav{
  &__filter-btn{
    display: none;
  }
}

.sort{
  display: flex;
  align-items: center;
  &__items{
    display: flex;
    align-items: center;
  }
  .sort-item{
    margin-left: 1em;
  }
}

.sort-item{
  label{
    color: $gray;
    display: flex;
    align-items: center;
    &:after{
      content: '';
      background-image: url(../img/sort.svg);
      @include bgImgSize(100%);
      opacity: 0;
      width: 0;
      height: 0;
    }
  }
  input:checked ~ label{
    color: $black;
    &:after{
      width: 1.125em;
      height: 1.125em;
      margin-left: .625em;
      opacity: 1;
    }
  }
}

.sort-dropdown{
  &__show{
    display: none;
  }
}

.more-btn{
  width: 100%;
  margin-top: 2.5em;
}


@media screen and (max-width: 992px){
  .filter{
    &__grid{
      flex-direction: column;
    }
  }
}


@media screen and (max-width: 576px){
  .free-ship{
    &__item{
      height: 6.5em;
    }
  }

  ._filter-opened{
    .filter{
      opacity: 1;
      pointer-events: unset;
    }
  }

  .filter{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $white;
    z-index: 100;
    padding-top: 3.5em;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    &__top{
      position: fixed;
      top: 0;
      left: 0;
      height: 3.5em;
      width: 100%;
      padding: 0 1em;
      z-index: 10;
      background-color: $white;
      border-bottom: $border;
      display: flex;
      align-items: center;
      justify-content: space-between;
      .h5{
        @include centerAbsolute;
      }
    }
    &__close{
      width: 1.5em;
      @include center;
      img{
        width: 100%;
      }
    }
    &__scroll{
      height: 100%;
      overflow-y: auto;
      padding: 1.5em 1em;
    }
  }

  .catalog-nav{
    min-height: unset;
    &__filter-btn{
      display: flex;
      height: 3em;
      width: 100%;
      i{
        margin-left: .75em;
        background-color: $primary;
        color: $white;
        border-radius: 12.5em;
        min-width: 1.25em;
        height: 1.25em;
        padding: 0 .125em;
        font-weight: 600;
        font-style: normal;
        @include center;
        span{
          font-size: .75em;
        }
      }
    }
  }

  .filter2{
    &__title{
      display: none;
    }
  }

  .sort{
    &__items{
      display: block;
    }
    .sort-item{
      margin: 0;
    }
  }

  .sort-item{
    label{
      padding: .625em;
      &:after{
        display: none;
      }
    }
    input:checked~label{
      color: $black;
    }
  }

  .sort-dropdown{
    margin-left: 1em;
    position: relative;
    z-index: 3;
    &__show{
      display: flex;
      align-items: center;
      cursor: pointer;
      img{
        width: 1.125em;
        margin-left: .625em;
      }
    }
    &__hidden{
      position: absolute;
      top: 100%;
      margin-top: .375em;
      left: -.875em;
      width: 13.75em;
      background-color: $white;
      box-shadow: 0 6px 16px rgba($black,.14);
      padding: .25em;
      border-radius: .375em;
      transition: all .3s;
      opacity: 0;
      pointer-events: none;
    }
    &._open{
      .sort-dropdown__hidden{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
}
