.catalog-detail{
  padding: 3em 0 1.5em;
  overflow: hidden;
  .tags__items{
    margin-top: 1em;
  }
  .tag-item{
    padding: .5em 1.5em;
    height: unset;
    margin: .5em .5em 0 0;
    span{
      font-size: .875em;
      line-height: 157%;
    }
  }
}

.catalog-brands{
  &__toggle{
    ._view2{
      display: none;
    }
  }
  &._show-all{
    .catalog-brands__toggle{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
}

.catalog-brands{
  &__grid{
    display: grid;
    grid-template-columns: repeat(8,calc(12.5% - 7em / 8));
    gap: 1em;
  }
  &:not(._show-all){
    .brand-card:nth-child(8)~.brand-card{
      display: none;
    }
  }
}

.catalog-brands2{
  margin-top: 1.5em;
  .brand-card{
    .brand-card__img{
      img{
        width: auto;
        max-width: 100%;
        max-height: 100%;
      }
    }
  }
  @media screen and (min-width: 993px){
    .brand-card{
      .brand-card__img{
        padding: 0;
        height: 9.25em;
      }
    }
    .catalog-brands__grid{
      grid-template-columns: repeat(6,calc(100% / 6 - 5em / 6));
    }
    &:not(._show-all){
      .brand-card:nth-child(6)~.brand-card{
        display: none;
      }
    }
  }
}




@media screen and (max-width: 992px){
  .catalog-detail{
    padding: 2.5em 0 2em;
  }

  .catalog-brands{
    &__grid{
      grid-template-columns: repeat(5,calc(20% - 4em / 5));
    }
    &:not(._show-all){
      .brand-card:nth-child(5)~.brand-card{
        display: none;
      }
    }
  }


}


@media screen and (max-width: 576px){
  .catalog-detail{
    padding: 1.5em 0;
  }

  .catalog-brands{
    &__grid{
      grid-template-columns: repeat(3,calc(100% / 3 - .5em));
      gap: 1em .75em;
    }
    .brand-card:nth-child(6){
      display: block !important;
    }
    &:not(._show-all){
      .brand-card:nth-child(6)~.brand-card{
        display: none;
      }
    }
  }


}
