.text{
  @include grid100gap(1.5em);
  ._bold{
    li:before{
      font-weight: 700;
    }
  }
  ul:not(.m-list),ol{
    padding-left: .625em;
    li{
      position: relative;
      padding-left: .875em;
      &:before{
        position: absolute;
        top: 0;
        left: 0;
      }
    }
  }
  ol {
    counter-reset: list;
    padding-left: 0;
    li {
      counter-increment: list;
      padding-left: 1.125em;
      &:before {
        content: counter(list) '. ';
      }
    }
  }
  ul:not(.m-list){
    li{
      &:before{
        content: '';
      }
    }
  }
  hr{
    display: block;
    opacity: 0;
    margin: 0;
    height: 0;
  }
  img{
    display: block;
    width: 100%;
    border-radius: .75em;
  }
  h6{
    font-weight: 600;
  }
}



@media screen and (max-width: 992px){
  .text{
    p, ol li, ul:not(.m-list) li{
      font-size: .875em;
      line-height: 143%;
    }
    ul:not(.m-list){

    }
  }
}


