.header{
  position: relative;
  z-index: 10;
  &._open-catalog{
    .header-catalog{
      opacity: 1;
      pointer-events: unset;
    }
    .header-catalog__bg{
      display: block;
    }
    .catalog-btn{
      ._view1{
        opacity: 0;
      }
      ._view2{
        opacity: 1;
      }
    }
  }
}

.header-catalog{
  position: absolute;
  top: 7.5em;
  left: 0;
  width: 100%;
  z-index: 10;
  background-color: $white;
  color: $black;
  padding: 0 19.5em;
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  &__bg{
    display: none;
  }
  &__inner{
    display: grid;
    grid-template-columns: 24em calc(100% - 24em);
  }
  &__col{
    padding: 2.5em 2.5em 2.5em 0;
  }
  &__col2{
    padding: 2.5em 0 2.5em 2.5em;
    border-left: $border;
    display: flex;
    flex-direction: column;
  }
  &__item{
    padding: .5em 1em;
    display: flex;
    align-items: center;
    cursor: pointer;
    transition: all .3s;
    border-radius: .1875em;
    img{
      width: 1.875em;
      margin-right: .625em;
    }
  }
  &__top{
    display: none;
  }
  @media screen and (min-width: 577px){
    &__col2{
      @include scroll;
      max-height: 40em;
      overflow-y: auto;
    }
    ._hide-pc{
      display: none;
    }
    &__items{
      padding-bottom: 1em;
      max-height: 31.25em;
      overflow-y: auto;
      padding-right: 1em;
      @include scroll;
    }
    &__item{
      border-radius: .25em;
      &._active,
      &:hover{
        background-color: $bg;
      }
    }
  }
}

.submenu{
  display: none;
  &__back{
    display: none;
  }
  &._active{
    display: block;
  }
  &__items{
    margin-top: 1.25em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2em / 3));
    gap: 1em;
  }
  a{
    font-size: 1em;
    line-height: 150%;
  }
}


@media screen and (max-width: 992px){
  .header-catalog{
    box-shadow: none;
    height: calc(100 * var(--vh) - 12.125em);
    position: fixed;
    bottom: 3.375em;
    left: 0;
    top: unset;
    border-radius: 0;
    padding: 0;
    &__inner{
      grid-template-columns: 16.25em calc(100% - 16.25em);
      height: 100%;
    }
    &__col{
      padding: 1.5em 1.5em 1.5em 1.875em;
    }
    &__col2{
      padding-left: 1.875em;
      height: 100%;
      overflow-y: auto;
      max-height: $vh;
      @include scroll;
    }
    @media screen and (min-width: 577px) {
      &__col2 {
        height: calc(100 * var(--vh) - 12.125em);
        padding: 1.5em;
        padding-right: 1.875em;
      }
    }
    &__items{
      height: calc(100 * var(--vh) - 17.875em);
    }
    &__top{
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      padding: 1em 1.875em;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &__close{
      width: 1.5em;
      @include center;
      margin-left: auto;
      img{
        width: 100%;
      }
    }
    @media screen and (min-width: 577px){
      &__top{
        position: static;
        padding: 0;
      }
      &__close{
        position: absolute;
        top: 1em;
        right: 1.875em;
        z-index: 2;
        display: none;
      }
    }
    &__back{
      display: none;
    }
  }

  .submenu{
    &__items{
      grid-template-columns: repeat(2,calc(50% - .5em));
    }
    a{
      font-size: .875em;
      line-height: 143%;
    }
  }

  .header{
    @media screen and (min-width: 577px){
      &._open{
        .header-catalog{
          opacity: 1;
          pointer-events: unset;
        }
        .header-catalog__bg{
          display: block;
        }
        .menu{
          pointer-events: unset;
          opacity: 1;
        }
        .burger{
          ._view1{
            opacity: 0;
          }
          ._view2{
            opacity: 1;
          }
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .header-catalog{
    bottom: 0;
    height: calc(100 * var(--vh) - 3.75em);
    padding: 1em;
    &__top{
      z-index: 3;
      position: absolute;
      top: 1em;
      padding: 0 1em;
    }
    &__back{
      display: flex;
      align-items: center;
      color: $gray;
      opacity: 0;
      pointer-events: none;
      img{
        width: .75em;
        margin-right: .5em;
      }
    }
    &__inner {
      display: block;
      z-index: 1;
    }
    &__col{
      padding: 0;
    }
    &__col2{
      padding: 0;
      height: unset;
      max-height: unset;
      border: none;
    }
    &__items{
      margin-top: 1em;
      height: calc(100 * var(--vh) - 8em);
      overflow-y: auto;
      @include scroll;
    }
    &__item{
      padding: .5em 0;
      border-bottom: $border;
      border-radius: 0;
    }
  }

  .submenu._active,
  .submenu{
    display: none;
  }

  .submenu{
    &__items{
      grid-template-columns: 100%;
      gap: 0;
      margin-top: 1.25em;
      height: calc(100 * var(--vh) - 8em);
      overflow-y: auto;
      @include scroll;
    }
    li{
      padding: .69em 0;
    }
    &__back{
      @include center;
      position: relative;
      z-index: 7;
      width: 1em;
      margin-right: .5em;
      img{
        width: 100%;
      }
    }
  }

  .header{
    &._open{
      .menu{
        pointer-events: unset;
        opacity: 1;
      }
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
    }
  }

  .header._open-sub-catalog{
    .header-catalog__col{
      display: none;
    }
    .submenu._active{
      display: block;
    }
    .header-catalog__back{
      opacity: 1;
      pointer-events: unset;
    }
  }
}
