.delivery-main{
  overflow: hidden;
  &__desc{
    margin-left: 2.5em;
    img{
      margin-right: 1em;
      width: 1.5em;
    }
  }
  .text{
    max-width: 67.25em;
  }
}

.info-nav{
  &__link{
    color: $black;
    &._active{
      pointer-events: none;
      background-color: $primary;
      color: $white;
    }
  }
  .swiper-container{
    width: 100%;
  }
  .swiper-slide{
    width: auto;
    padding-right: 0;
    &:not(:last-child){
      .m-btn{
        border-right: none;
      }
    }
  }
}

.order-guide{
  max-width: 67.25em;
}


@media screen and (max-width: 992px){
  .delivery-main{
    &__desc{
      margin-left: 1.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .delivery-main{
    &__top{
      display: block;
    }
    &__desc{
      margin: 1.5em 0 0;
    }
  }
}
