.main{
  padding-bottom: 7.125em;
  position: relative;
  margin-bottom: .5em;
  color: $white;
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    img{
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
    }
    &:after{
      background: linear-gradient(270deg, rgba(0, 0, 0, 0.00) 0%, #000 100%);
    }
  }
  &__content{
    max-width: 48.25em;
    margin-top: 8.75em;
    position: relative;
  }
  &__shops{
    position: absolute;
    top: -1em;
    right: 4em;
    display: flex;
    align-items: center;
    gap: 1em;
    padding: .75em 1.5em;
    border-radius: 12.5em;
    border: 1px solid rgba(255, 255, 255, 0.20);
  }
}

.main-rating{
  display: flex;
  align-items: flex-end;
  &__ball{
    width: 3.75em;
    position: relative;
    flex-shrink: 0;
    margin-right: 1.25em;
    img{
      width: 100%;
      display: block;
    }
    &-num{
      @include centerAbsolute;
    }
  }
  &__content{
    max-width: 12.75em;
  }
  .rating{
    margin-bottom: .375em;
  }
  &__text{
    font-size: .875em;
    line-height: 143%;
  }
}


@media screen and (max-width: 992px){
  .main{
    padding-bottom: 4em;
    &__content{
      margin-top: 4.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    padding-bottom: 2.5em;
    &__content{
      margin-top: 2.875em;
    }
    &__shops{
      position: static;
      gap: 0;
      justify-content: space-between;
      padding: .75em 1.25em;
      margin-bottom: 1.5em;
    }
  }
}
