.header-search{
  position: fixed;
  z-index: 101;
  background-color: $white;
  padding: 5em 0;
  width: 100%;
  top: 0;
  left: 0;
  transition: all .3s;
  pointer-events: none;
  opacity: 0;
  &__bg{
    z-index: 100;
    display: none;
  }
  &__inner{
    display: flex;
    align-items: center;
  }
  .m-search{
    flex-grow: 1;
    &__btn{
      right: unset;
      left: 0;
    }
  }
  .fg{
    height: 3.5em;
    input{
      border: none;
      border-bottom: $border;
      font-size: 1.5em;
      height: 100%;
      padding: 0 1.67em;
    }
  }
  &__btn{
    width: 12.5em;
    height: 3.5em;
    margin: 0 2.5em;
    flex-shrink: 0;
  }
  &__close{
    @include center;
    width: 1.5em;
    img{
      width: 100%;
    }
  }
}

.header{
  &._open-search{
    .header-search{
      opacity: 1;
      pointer-events: unset;
    }
    .header-search__bg{
      display: block;
    }
  }
}


@media screen and (max-width: 992px){
  .header-search{
    padding: 3em 0;
    .fg{
      height: 2.375em;
      input{
        font-size: 1.125em;
        padding: 0 2.22em;
      }
    }
    &__btn{
      width: 8.75em;
      height: 3.375em;
    }
  }
}


@media screen and (max-width: 576px){
  .header-search{
    padding: 2em 0;
    &__inner{
      flex-wrap: wrap;
    }
    .m-search{
      width: calc(100% - 2.5em);
    }
    &__close{
      margin-left: 1em;
    }
    &__btn{
      order: 2;
      width: 100%;
      margin: 2em 0 0;
    }
  }
}
