.menu{
  position: absolute;
  bottom: 0;
  left: 19.5em;
  height: 3.75em;
  display: flex;
  width: calc(100% - 34.5em);
  padding-left: 11.5em;
  z-index: 11;
  &__consult{
    height: 3.75em;
    padding: 0 2.5em;
  }
  .socials{
    display: none;
  }
}

.catalog-btn{
  position: absolute;
  bottom: 0;
  left: 19.5em;
  width: 11.5em;
  height: 3.75em;
  @include center;
  font-weight: 500;
  z-index: 11;
  &__img{
    width: 1.5em;
    flex-shrink: 0;
    margin-right: .75em;
    position: relative;
    img{
      width: 1.5em;
      @include centerAbsolute;
      transition: all .3s;
    }
    ._view2{
      opacity: 0;
    }
  }
}

.nav{
  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-weight: 500;
  padding: 0 3em;
  &__link{
    margin-right: 2em;
    &:last-child{
      margin-right: 0;
    }
  }
}

.nav__link,
.nav-item__top{
  span{
    font-size: 1em;
    line-height: 125%;
  }
}

.nav-item{
  position: relative;
  margin-right: 2em;
  &__top{
    cursor: pointer;
    display: flex;
    align-items: center;
    &:after{
      content: '';
      margin-left: .375em;
      flex-shrink: 0;
      width: .75em;
      height: .75em;
      background-image: url(../img/arrow-dropdown.svg);
      @include bgImgSize(100%);
    }
  }
  &__list{
    a{
      font-size: .875em;
      line-height: 143%;
      padding: .5em;
      border-radius: .25em;
      display: flex;
      align-items: center;
      justify-content: space-between;
      &:hover{
        color: $primary;
      }
    }
    li{
      &:last-child{
        border-bottom: none;
      }
    }
  }
  @media screen and (min-width: 577px){
    &:hover{
      .nav-item__bottom{
        opacity: 1;
        pointer-events: unset;
      }
    }
    &__bottom{
      color: $black;
      position: absolute;
      top: 100%;
      left: -1.5em;
      padding-top: .75em;
      margin-top: -1px;
      width: auto;
      white-space: nowrap;
      z-index: 4;
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
      display: block !important;
    }
    &__inner{
      padding: 1em;
      background-color: $white;
      border-radius: .375em;
      box-shadow: 0 6px 16px 0 rgba(20,20,20,.1);
    }
  }
}


@media screen and (max-width: 992px){
  .menu{
    opacity: 0;
    pointer-events: none;
  }
  @media screen and (min-width: 577px){
    .menu{
      position: static;
      width: auto;
      height: unset;
      &__consult{
        position: fixed;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 3.375em;
        z-index: 11;
      }
    }

    .nav{
      width: 100%;
      height: 3.75em;
      padding: 0 1.875em;
      border-top: $border;
      border-bottom: $border;
      position: fixed;
      top: 5em;
      left: 0;
      background-color: $white;
      z-index: 11;
    }

    .catalog-btn{
      display: none;
    }
  }
}


@media screen and (max-width: 576px){
  .menu{
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    height: calc(100 * var(--vh) - 3.75em);
    background-color: $white;
    border-top: $border;
    padding: 0 0 1em;
    display: block;
    z-index: 5;
    overflow-y: auto;
    &__consult{
      width: 100%;
      height: 3.375em;
      span{
        font-size: .875em;
      }
    }
    .socials{
      display: flex;
      justify-content: center;
      margin-top: 1.5em;
    }
  }

  .nav{
    display: block;
    padding: 0;
    &__link{
      border-bottom: $border;
      margin: 0;
      padding: 1em;
      &:hover{
        color: $black;
      }
      &:last-child{
        margin: 0;
      }
    }
  }

  .nav__link,
  .nav-item__top{
    display: flex;
    align-items: center;
    span{
      font-size: .875em;
      line-height: 143%;
      font-weight: 600;
    }
  }

  .nav-item{
    border-bottom: $border;
    margin: 0;
    &._active{
      .nav-item__top:after{
        transform: rotate(-180deg);
      }
    }
    &__top{
      justify-content: space-between;
      padding: 1em;
      &:after{
        width: 1em;
        height: 1em;
      }
    }
    &__list{
      @include grid100gap(.75em);
      a{
        padding: 0;
      }
    }
    &__bottom{
      display: none;
      padding: 0 1em 1em;
    }
  }

  .catalog-btn{
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    z-index: 4;
    display: none;
  }
}
