.receiver{
  &__fields{
    @include grid100gap(1em);
  }
}

.order-delivery{
  &__radios{
    gap: 2.5em;
  }
}

.order-thank{
  padding: 4em 3em;
  background-color: $bg;
  &__items{
    display: flex;
    justify-content: center;
    gap: 1em 4em;
  }
  .copy-btn{
    margin-left: .75em;
  }
  &__prices{
    .m-badge{
      margin-left: -.5em;
    }
  }
  &__nav{
    gap: .75em;
  }
  &__link{
    gap: .375em;
    img{
      margin: 0 !important;
      width: 2em;
    }
  }
}

.manager{
  &__inner{
    padding: 3em;
    border: $border;
  }
  &__items{
    display: flex;
    justify-content: center;
    gap: 2.5em;
  }
  .socials__item{
    width: 2em;
    height: 2em;
    @include lastM(0 .5em 0 0);
  }
}


@media screen and (max-width: 992px){
  .receiver{
    &__fields{
      grid-template-columns: repeat(2,calc(50% - .5em));
    }
  }

  .order-thank{
    padding: 3em;
  }
}


@media screen and (max-width: 576px){
  .receiver{
    &__fields{
      grid-template-columns: 100%;
      gap: .75em;
    }
  }

  .order-delivery{
    &__radios{
      gap: 1em;
      flex-direction: column;
    }
  }

  .order-thank{
    padding: 1.25em;
    &__items{
      flex-direction: column;
    }
    &__nav{
      display: grid;
      grid-template-columns: 100%;
    }
    &__link{
      padding: 0;
    }
  }

  .manager{
    &__inner{
      padding: 1.25em;
    }
    &__items{
      flex-direction: column;
      align-items: center;
      gap: 1em;
      text-align: center;
    }
    .socials{
      justify-content: center;
    }
  }
}
