.pagination{
  margin-top: 2.5em;
  display: flex;
  justify-content: center;
  ul{
    display: flex;
    justify-content: center;
  }
  li{
    margin: 0 .375em;
  }
  .page-numbers,
  a{
    font-size: .875em;
    height: 3.43em;
    min-width: 3.43em;
    padding: 0 .5em;
    @include center;
    border: $border;
    &:hover{
      color: $primary;
    }
  }
  .current a{
    background-color: $primary;
    border-color: $primary;
    color: $white;
    pointer-events: none;
  }
  .prev,
  .next{
    @include bgImgSize(.75em);
    a{
      border: none;
    }
  }
  .prev{
    background-image: url(../img/prev.svg);
  }
  .next{
    background-image: url(../img/next.svg);
  }
}



