}


@media screen and (max-width: 576px){
  .more-btn,
  .pagination{
    margin-top: 2em;
  }

  .pagination{
    ul{

    }
    li{
      margin: 0 .25em;
    }
    .page-numbers,
    a{
      font-size: .75em;
    }
    ._hide-mob{
      display: none;
    }
  }
}
