.cart{
  &__col{
    grid-column: span 3;
  }
}

.cart-card{
  display: grid;
  grid-template-columns: 6.875em calc(100% - 25.75em - 6.875em) 22.75em;
  gap: 1.5em;
  padding-bottom: 2.5em;
  margin-bottom: 2.5em;
  border-bottom: $border;
  &:last-child{
    border-bottom: none;
    padding-bottom: 0;
    margin-bottom: 0;
  }
  &__img{
    @include imgCover(133%);
  }
  &__desc{
    @include truncate(2);
  }
  &__props{
    @include grid100gap(.25em);
  }
  &__prop{
    display: flex;
    gap: .25em;
    flex-wrap: wrap;
  }
  &__action{
    min-height: 5em;
  }
  &__sale{
    margin-top: .25em;
    .m-badge{
      margin-left: .5em;
    }
  }
  &__delete{
    width: 1.25em;
    @include center;
    img{
      width: 100%;
    }
  }
}

.cart-sidebar{
  position: sticky;
  top: 1em;
}

.cart-info{
  padding: 1.5em;
  background-color: $bg;
  &__props{
    @include grid100gap(.5em);
  }
  &__btn{
    width: 100%;
  }
}

.cdek{
  padding: 1.5em;
  color: $white;
  background: #00B33C;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__content{
    display: flex;
    align-items: center;
    gap: .5em 1em;
  }
  &__img{
    width: 6.125em;
  }
}

.order-card{
  @media screen and (min-width: 993px) {
    grid-template-columns: 6.875em calc(100% - 18.875em) 9em;
    .cart-card__props{
      display: flex;
      gap: 1.5em;
    }
  }
}

.order-cdek{
  @media screen and (min-width: 993px){
    .cdek{
      flex-direction: column;
      align-items: flex-start;
      &__content{
        flex-direction: column;
        align-items: flex-start;
        order: 2;
        margin-top: 2em;
      }
      .h5 {
        font-size: 1.5em;
        line-height: 133%;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .cart{
    &__col{
      grid-column: span 1;
    }
  }

  .cart-card{
    grid-template-columns: 6.125em calc(100% - 7.625em);
    @media screen and (min-width: 577px){
      position: relative;
      &__col2{
        padding-right: 2.75em;
      }
      &__col3{
        grid-column: 1/-1;
        padding-left: 7.625em;
        margin-top: -1.5em;
      }
      &__props{
        display: flex;
        gap: 1.5em;
      }
      &__action{
        justify-content: unset;
        gap: 2.5em;
      }
      &__prices{
        display: flex;
        align-items: center;
      }
      &__sale{
        margin: 0 0 0 1em;
      }
      &__delete{
        position: absolute;
        top: 0;
        right: 0;
      }
      &__shops{
        display: flex;
        align-items: center;
        gap: .75em;
        .m-shops{
          margin: 0;
        }
      }
    }
    &__action{
      min-height: unset;
    }
  }

  .cdek{
    &__content{
      flex-direction: column;
      align-items: flex-start;
    }
  }

  .order-card{
    .cart-card__col2{
      padding: 0;
    }
  }
}


@media screen and (max-width: 576px){
  .cart-card{
    padding-bottom: 2em;
    margin-bottom: 2em;
    grid-template-columns: 3.5em calc(100% - 4.5em);
    gap: 1em;
    &__col3{
      grid-column: 1/-1;
      padding-left: 4.5em;
    }
  }

  .cdek{
    flex-direction: column;
    align-items: flex-start;
    &__content{
      order: 2;
      margin-top: .5em;
    }
  }

  .order-card{
    .cart-card{
      &__prices{
        display: flex;
        align-items: center;
      }
      &__sale{
        margin: 0 0 0 1em;
      }
    }
  }
}
