.header{
  position: relative;
  z-index: 11;
  font-size: calc(100vw * (16 / 1920));
  &__inner{
    display: grid;
    grid-template-columns: 19.5em calc(100% - 34.5em) 15em;
    align-items: center;
    position: relative;
    z-index: 11;
    background-color: $white;
    border-bottom: $border;
  }
  &__logo{
    @include center;
    img{
      flex-shrink: 0;
    }
    &-img{
      width: 3.75em;
      margin-right: 1em;
    }
    &-img2{
      width: 10.25em;
    }
  }
  &__content{
    align-self: flex-start;
  }
  &__top{
    height: 3.75em;
    border-left: $border;
    border-right: $border;
    padding: 0 2.5em;
  }
}

.header-index{
  .header__inner{
    border-bottom: none;
  }
}

.header-action{
  display: grid;
  grid-template-columns: 50% 50%;
  &__btn{
    height: 7.5em;
    @include center;
    img{
      width: 1.5em;
    }
  }
}

.header-action__btn--phone,
.burger{
  display: none;
}

.header-city{
  &__show{
    img{
      width: 1em;
      margin-right: .5em;
    }
  }
}

.header-contacts{
  .socials{
    margin-left: .75em;
    &__item{
      @include lastM(0 .5em 0 0);
      width: 2em;
      height: 2em;
    }
  }
  &__info{
    margin-left: 2em;
  }
  &__phone{
    margin-right: 2em;
  }
}


@media screen and (max-width: 992px){
  .header{
    font-size: 1em;
    &__inner{
      grid-template-columns: 13em 5em calc(100% - 28em) 10em;
    }
    &__logo{
      &-img{
        width: 2.1875em;
        margin-right: .75em;
      }
      &-img2{
        width: 7em;
      }
    }
    &__top{
      height: 5em;
      border-left: none;
      padding: 0 1.75em;
    }
    &__bottom{
      display: none;
    }
  }

  .header-city{
    display: none;
  }

  .header-action{
    &__btn{
      height: 5em;
    }
  }

  .header-contacts{
    width: 100%;
    &__support{
      &-title{
        display: none;
      }
    }
    &__info{
      display: block;
      margin-left: auto;
      text-align: right;
    }
    &__phone{
      margin: 0 0 .125em;
    }
  }

  .burger{
    @include center;
    height: 5em;
    position: relative;
    img{
      width: 1.5em;
      @include centerAbsolute;
      transition: all .3s;
    }
    ._view2{
      opacity: 0;
    }
  }
}


@media screen and (max-width: 576px){
  .header{
    &__inner{
      grid-template-columns: 3.75em 3.75em calc(100% - 15em) 7.5em;
    }
    &__logo{
      grid-column: 3/4;
      &-img{
        width: 1.75em;
        margin-right: 0;
      }
      &-img2{
        display: none;
      }
    }
    &__content{
      display: none;
    }
    &__bottom{
      display: none;
    }
  }

  .header-contacts{
    display: none;
  }

  .burger{
    height: 3.75em;
    order: -1;
    img{
      width: 1.25em;
    }
  }

  .header-action{
    &__btn{
      height: 3.75em;
      img{
        width: 1.25em;
      }
      &--phone{
        display: flex;
      }
      &--search{
        position: absolute;
        top: 0;
        left: 5em;
      }
    }
  }
}
