.review-img{
  min-height: 100%;
  &__inner{
    @include imgCover(108%);
    height: 100%;
  }
}

.review-gallery{
  height: 100%;
  .slider{
    height: 100%;
  }
  .swiper-container{
    height: 100%;
  }
  .swiper-wrapper{
    height: 100%;
  }
  @media screen and (min-width: 577px){
    .swiper-container{
      width: 100%;
    }
    .swiper-wrapper{
      transform: unset !important;
      display: grid;
      gap: 1.5em;
      grid-template-columns: repeat(2,calc(50% - .75em));
      grid-template-rows: repeat(2,calc(50% - .75em));
    }
    .swiper-slide{
      padding: 0;
      &:nth-child(4)~.swiper-slide{
        display: none;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .review-gallery{
    grid-column: 1/-1;
    @media screen and (min-width: 577px){
      .swiper-wrapper{
        grid-template-columns: repeat(4,calc(25% - 4.5em / 4));
        grid-template-rows: unset;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .reviews{
    overflow: hidden;
    &__grid{
      &>.review-img{
        display: none;
      }
    }
  }

  .review-gallery{
    .swiper-container{
      width: calc(100% + .75em);
    }
    .swiper-slide{
      width: 8.5em;
      padding-right: .75em;
    }
  }
}
