.selected{
  margin-top: -1.5em;
  .swiper-slide{
    display: flex;
    align-items: center;
  }
  &__brand{
    flex-shrink: 0;
    width: 1.375em;
    @include center;
    margin-right: .5em;
    img{
      width: 100%;
    }
  }
  &__item{
    display: flex;
    align-items: center;
    gap: 1em;
    margin-right: 1em;
    &-title{
      white-space: nowrap;
      max-width: 48em;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }
  &__btn{
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    z-index: 2;
    img{
      width: 1.125em;
      margin-right: .625em;
    }
  }
  @media screen and (min-width: 577px){
    &__brand{
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
    }
    .swiper-slide{
      padding-left: 1.875em;
      position: relative;
      margin-top: -.5em;
      flex-wrap: wrap;
    }
    &__item{
      margin-top: .5em;
    }
    .swiper-wrapper{
      transform: unset !important;
    }
  }
}


@media screen and (max-width: 992px){
  .selected{
    &__item{
      height: 2.625em;
      max-width: calc(100% - .5em);
      border: $border;
      padding: 0 1em;
      &-line{
        display: none;
      }
    }
    &__btn{
      padding: 0;
      position: static;
      transform: unset;
      width: 100%;
      justify-content: center;
      margin-top: 1em;
      border: 1px solid $primary;
      height: 2.625em;
    }
    &__brand{
      width: 2.5em;
    }
    @media screen and (min-width: 577px){
      .swiper-slide{
        padding-left: 4em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .selected{
    margin-top: 0;
    &__brand{
      margin-right: 1em;
    }
    &__item{
      padding: 0 .9375em;
    }
    .swiper-slide{
      width: auto;
    }
  }
}
