.product-current{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 2em;
  padding-top: 2em;
  border-top: $border;
  &__col{
    width: 5.75em;
    flex-shrink: 0;
    margin-right: 1em;
  }
  &__col2{
    flex-grow: 1;
  }
  &__img{
    @include imgCover(76%);
  }
  &__nav{
    display: flex;
    margin-top: .25em;
  }
}

.product-cart{
  &__available{
    display: flex;
    align-items: center;
    &-round{
      width: .375em;
      height: .375em;
      border-radius: 50%;
      flex-shrink: 0;
      margin-right: .5em;
    }
  }
  &__prices{
    white-space: nowrap;
    flex-wrap: wrap;
  }
  .m-badge{
    margin-left: .5em;
  }
  &__nav{
    @include grid100gap(.75em);
    .m-btn-blue-outline{
      img{
        width: 2em !important;
      }
    }
  }
  &__count{
    img{
      width: 1.5em;
      margin-right: .75em;
    }
  }
}

.m-shops{
  display: flex;
  gap: .5em 1em;
  flex-wrap: wrap;
  &__item{
    display: flex;
    align-items: center;
    img{
      width: 1.5em;
      margin-right: .5em;
    }
  }
}

.product-advantages{
  border: $border;
  padding: 1.25em;
  @include grid100gap(1em);
  &__item{
    display: flex;
    align-items: center;
    img{
      width: 1.25em;
      margin-right: .5em;
    }
  }
}


