.product-collection{
  &__grid{
    row-gap: 2.5em;
  }
  &__more{
    ._view2{
      display: none;
    }
  }
  &._show-all{
    .product-collection__more{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
  &:not(._show-all){
    .product-card:nth-child(4)~.product-card{
      display: none;
    }
  }
}

.product-card{
  display: flex;
  flex-direction: column;
  &__img{
    @include imgCover(133%);
  }
  &__bottom{
    margin-top: auto;
    padding-top: 1em;
  }
  &__btn{
    height: 3em;
  }
}

.m-badge{
  padding: 0 .5em;
  border-radius: 1.875em;
  background-color: $red;
  color: $white;
  height: 1.25em;
  @include center;
  font-weight: 600;
}

.brands{
  overflow: hidden;
}

.brands-slider{
  .swiper-container{
    width: calc(100% + 1em);
  }
  .swiper-slide{
    width: 12.5%;
    padding-right: 1em;
  }
}

.brand-card{
  text-align: center;
  display: block;
  &__img{
    border: $border;
    padding-bottom: 100%;
    position: relative;
    img{
      @include centerAbsolute;
      width: 5em;
    }
  }
}

.advantage-card{
  border: $border;
  padding: 1.25em;
  display: flex;
  align-items: center;
  &__icon{
    margin-right: 1em;
    width: 4em;
    @include center;
    flex-shrink: 0;
    img{
      width: 2.5em;
    }
  }
}



@media screen and (max-width: 992px){
  .product-collection{
    &:not(._show-all){
      .product-card:nth-child(3)~.product-card{
        display: none;
      }
    }
  }

  .product-card{
    &__btn{
      height: 2.875em;
    }
  }

  .brands-slider{
    .swiper-slide{
      width: 20%;
    }
  }

  .advantage-card{
    display: block;
    &__icon{
      margin: 0 0 1em;
      width: auto;
      height: 2em;
      justify-content: unset;
      img{
        width: auto;
        height: 100%;
      }
    }
  }

}


@media screen and (max-width: 576px){
  .product-collection{
    &__top{
      display: block;
    }
    &__link{
      margin-top: 1.5em;
      width: 100%;
    }
    &__grid{
      row-gap: 2em;
    }
    &:not(._show-all){
      .product-card:nth-child(2)~.product-card{
        display: none;
      }
    }
  }

  .product-card{
    &__bottom{
      flex-direction: column;
      align-items: flex-start;
    }
    &__btn{
      width: 100%;
    }
    .m-badge{
      display: none;
    }
  }

  .brands-slider{
    .swiper-container{
      width: calc(100% + .75em);
    }
    .swiper-slide{
      width: calc(100% / 3);
      padding-right: .75em;
    }
  }

  .advantage-card{
    display: flex;
    &__icon{
      margin: 0 1em 0 0;
      width: 2em;
      height: auto;
      img{
        width: 100%;
        height: auto;
      }
    }
  }
}
