.catalog-item{
  padding-bottom: 1em;
  &__inner{
    display: grid;
    grid-template-columns: 26em calc(100% - 48.125em) 19.125em;
    gap: 1.5em;
  }
}

.gallery{
  overflow: hidden;
  &__img{
    @include imgCover(133%);
  }
  ._hide{
    display: none;
  }
  &__count{
    position: absolute;
    top: 0;
    right: 0;
    width: calc(100% / 6);
    height: 100%;
    z-index: 3;
    padding-right: 1em;
    &-inner{
      background: linear-gradient(0deg, rgba(0, 0, 0, 0.60) 0%, rgba(0, 0, 0, 0.60) 100%);
      width: 100%;
      height: 100%;
      color: $white;
      @include center;
    }
  }
  &__small{
    margin-top: 1em;
    position: relative;
    .swiper-wrapper{
      transform: unset !important;
    }
    .swiper{
      width: calc(100% +  1em);
      &-slide{
        width: calc(100% / 6);
        padding-right: 1em;
        &-thumb-active{
          .gallery__img{
            pointer-events: none;
            img{
              opacity: 1;
            }
          }
        }
      }
    }
    .gallery__img{
      cursor: pointer;
      padding: 0;
      height: 4.625em;
      img{
        transition: all .3s;
      }
    }
  }
}

.product-color{
  &__items{
    display: flex;
    flex-wrap: wrap;
    margin-top: .25em;
    width: calc(100% + .5em);
  }
}

.color-radio{
  margin: .5em .5em 0 0;
  &__img{
    width: 2.5em;
    height: 2.5em;
    border-radius: 50%;
    position: relative;
    pointer-events: none;
    background-size: cover;
    &:before{
      content: '';
      @include centerAbsolute;
      width: calc(100% + 2px);
      height: calc(100% + 2px);
      border: $border;
      border-radius: inherit;
      z-index: 2;
    }
  }
  input:checked ~ .color-radio__img:before{
    border-color: $primary;
    border-width: 2px;
  }
  input:disabled ~ .color-radio__img{
    opacity: .4;
    &:after{
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) rotate(45deg);
      width: 120%;
      height: 1px;
      background-color: $line;
    }
  }
}

.btn-radio{
  margin: .5em .5em 0 0;
  label{
    white-space: nowrap;
    @include center;
    height: 2.5em;
    border-radius: 12.5em;
    border: $border;
    padding: 0 1.4375em;
  }
  input:checked ~ label{
    border-color: $primary;
    outline: 1px solid $primary !important;
  }
}



@media screen and (max-width: 992px){
  .catalog-item{
    &__inner{
      grid-template-columns: 21.375em calc(100% - 22.875em);
    }
    &__col2{
      grid-column: 1/-1;
      order: 2;
    }
    ._hide-tablet{
      display: none;
    }
  }

  .gallery{
    &__count{
      width: 20%;
    }
    &__small{
      .swiper{
        &-slide{
          width: 20%;
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .catalog-item{
    overflow: hidden;
    &__inner{
      grid-template-columns: 100%;
    }
    &__col2{
      padding-top: 1.5em;
    }
  }

  .gallery{
    overflow: unset;
    &__count,
    &__small{
      display: none;
    }
    &__big{
      .swiper{
        width: calc(100% + .75em);
        overflow: unset;
      }
      .swiper-slide{
        width: 19.25em;
        padding-right: .75em;
      }
    }
  }
}
