.m-section{
  padding: 2.5em 0;
  &._first{
    padding-top: 0;
  }
  &__link{
    margin-left: 4em;
    white-space: nowrap;
  }
}

.box-card{
  @include center;
  border: $border;
  height: 5em;
  padding: 1.25em 1.5em;
  img{
    max-width: 100%;
    max-height: 100%;
  }
}

.m-search{
  position: relative;
  &__btn{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 1em;
    width: 1.5em;
    @include center;
    img{
      width: 100%;
    }
  }
  .fg{
    input{
      padding-right: 3.5em;
    }
  }
}

.map{
  height: 30em;
  overflow: hidden;
}

.block-grid{
  display: grid;
  grid-template-columns: repeat(2,calc(50% - 1.5625em));
  gap: 2.5em 3.125em;
  &--center{
    align-items: center;
  }
}

.m-img{
  img{
    width: 100%;
    display: block;
  }
}

.slider{
  position: relative;
  &__wrap{
    overflow: hidden;
  }
  .swiper-container{
    width: calc(100% + 1.5em);
  }
  .swiper-wrapper{
    align-items: stretch;
  }
  .swiper-slide{
    height: auto;
    padding-right: 1.5em;
  }
  .swiper-button{
    top: 50%;
    transform: translateY(-50%);
    &-prev{
      left: -4.625em;
    }
    &-next{
      right: -4.625em;
    }
  }
}

.swiper-pagination{
  @include center;
  margin-top: 2.5em;
  position: static;
  &-bullet{
    margin: 0 .5em !important;
    width: 0.75em;
    height: 0.75em;
    opacity: 1;
    border: $border;
    background-color: transparent;
    &-active{
      background-color: $primary;
      border-color: $primary;
    }
  }
  &._hide-pc{
    display: none;
  }
}


.reviews-collection,
.blog-collection{
  overflow: hidden;
}

.reviews-slider,
.blog-slider{
  .swiper-slide{
    width: calc(100% / 3);
  }
}

.blog-card{
  &__img{
    @include imgCover(56.25%);
  }
}


@media screen and (max-width: 992px){
  .m-section {
    padding: 2em 0;
  }

  .block-grid{
    grid-template-columns: 100%;
  }

  .req{
    @media screen and (min-width: 577px){
      &__col2{
        display: grid;
        grid-template-columns: repeat(2,calc(50% - .75em));
        gap: 0 1.5em;
      }
      .h2{
        grid-column: 1/-1;
      }
    }
    .h2{
      br{
        display: none;
      }
    }
    &__socials{
      flex-direction: column;
      align-items: flex-start;
      .socials{
        order: 2;
      }
    }
  }
  .swiper-pagination{
    &._hide-pc{
      display: flex;
    }
  }

  .slider{
    .swiper-button{
      display: none;
    }
  }

  .reviews-slider,
  .blog-slider{
    .swiper-slide{
      width: 22.375em;
    }
  }
}


@media screen and (max-width: 576px){
  .m-section {
    padding: 1.5em 0;
  }

  .box-card{
    height: 4em;
    padding: 1.25em;
  }

  .map{
    height: 15.5em;
  }

  .block-grid{
    gap: 2em;
  }

  .blog-collection{
    &__top{
      display: block;
    }
    &__link{
      width: 100%;
      margin-top: 1.5em;
    }
  }

  .swiper-pagination{
    margin-top: 2em;
  }

  .reviews-slider,
  .blog-slider{
    .swiper-slide{
      width: 100%;
    }
  }
}
